import {Component} from "react";
import Container from "react-bootstrap/Container";
import Card from "react-bootstrap/Card";
import React from "react";
import connect from "react-redux/es/connect/connect";
import {getArticles, gotoArticle, getArticle} from "../actions/article";
import ArticleTemplate6 from "../templates/ArticleTemplate6";
import {
    Link
} from "react-router-dom";
import SimpleArticle from "../templates/SimpleArticle";
import {Route} from "react-router-dom";
import Spinner from "react-bootstrap/Spinner";
import Button from "react-bootstrap/Button";
import CustomArticle from "../templates/CustomArticle";
import {Helmet} from "react-helmet";

class ArticlePage extends Component {

    constructor() {
        super();
        this.state = ({
            articleId:"",
        });
    }

    UNSAFE_componentWillMount(){
        if(this.props.article_info == null){
            let query = window.location.hash.split('#/article/')[1];
            this.setState({articleId:query});
            this.props.getArticle(query);
        }
        if(this.props.articles.length === 0){
            this.props.getArticles([],"");
        }


        // Handle browser back button
        window.onpopstate = () => {
            if(window.location.hash.startsWith('#/article/')){
                let query = window.location.hash.split('#/article/')[1];
                let article = this.props.articles.find(element => element._id === query);

                if(article !== null){
                    this.props.gotoArticle(article);
                }
                else{
                    this.props.getArticle(query);

                }
            }
        }
    }

    renderArticle() {
        //window.scrollTo(0,this.props.offsetTop);
        if(this.props.article_info) {
            switch (this.props.article_info.template) {
                case "Simple":
                    return (<SimpleArticle
                        editArticle={false}
                        showArticle={true}
                        title={this.props.article_info.title}
                        titleimg_url={this.props.article_info.titleimg_url}
                        author={this.props.article_info.author}
                        lead={this.props.article_info.lead}
                        sources={this.props.article_info.sources}
                        tags={this.props.article_info.tags}
                        layoutData={this.props.article_info.layoutData}
                        layoutDataCallback={(data) => console.log(data)}
                    />);
                case "Custom":
                    return (<CustomArticle
                        editArticle={false}
                        showArticle={true}
                        title={this.props.article_info.title}
                        titleimg_url={this.props.article_info.titleimg_url}
                        author={this.props.article_info.author}
                        lead={this.props.article_info.lead}
                        sources={this.props.article_info.sources}
                        tags={this.props.article_info.tags}
                        layoutData={this.props.article_info.layoutData}
                        layoutDataCallback={(data) => console.log(data)}
                    />);
                default:
                    return (<ArticleTemplate6 article_info={this.props.article_info}/>);
            }
        }
    }

    handleGotoArticle(article){
        this.props.gotoArticle(article);
        window.scrollTo(0,this.props.offsetTop);
        //window.scrollTo(0, 100);
    }

    renderArticlePreviews(){
        return(
            this.props.articles.map((article,index) => (
                (this.props.article_info == null ? this.state.articleId : this.props.article_info._id)!== article._id &&
                    <Card key={index} className="mt-4 bg-light" style={{border:'none'}} /*className={index===0 ? "mt-4" : "mt-4"}*/ /*style={{height:index===0 ? '' : '10rem', display: 'flex', flexDirection: 'column'}}*/>

                        <Card.Title className="mt-3 ml-3" style={{textAling:'left', fontWeight:'Bold', fontFamily:'Helvetica', fontSize:"1.5rem"}}>{article.title}</Card.Title>

                        <Card.Body className="row">
                            <Route render={({history}) => (
                                <Card.Img onClick={() => {
                                    history.push('/article/'+article._id);
                                    this.handleGotoArticle(article);
                                }} className="img-fluid col-md-4" style={{maxHeight:'20rem', width:'auto', objectFit:'cover'}} src={article.titleimg_url} />
                            )}/>
                            <Card.Text className="col-md-5" style={{fontFamily:'Helvetica'}}>{article.lead}</Card.Text>
                            <Link className="col-md-2 mr-0" to={"/article/"+article._id} style={{color:'#15632a', outline:'0'}} onClick={() => this.handleGotoArticle(article)}>Read more</Link>
                        </Card.Body>
                    </Card>
                )
            )
        )
    }

    render() {
        return (
            <div>
            <Helmet>
                {/*TODO IMPLEMENT OG AND OTHER TAGS*/}
                <title>{this.props.article_info === null ? "Finland China News" : this.props.article_info.title}</title>
            </Helmet>
            <Container>
                {this.props.loading &&
                    <div className="text-center m-5">
                        <Spinner animation="border" variant="success"/>
                    </div>
                }
                <Container className="bg-white" style={{}}>
                    {this.renderArticle()}
                </Container>
                <Container style={{marginTop:"5rem"}}>
                    <h4 style={{fontFamily:'Helvetica'}}>More articles:</h4>
                    {this.renderArticlePreviews()}
                </Container>
                {this.props.loading &&
                   <div className="text-center m-5">
                        <Spinner animation="border" variant="success"/>
                    </div>
                }
                {this.props.articlesAvailable && !this.props.loading &&
                    <Container style={{marginTop: '2rem', textAlign: 'center'}}>
                        <Button style={{background: '#15632a', fontWeight: 'Bold', outline: '0'}}
                                className="rounded-8 btn-sm btn-success"
                                onClick={() => {
                                    this.props.getArticles(this.props.articles.map(a => a._id), this.props.checkpoint);
                                }}
                        >Show more</Button>
                    </Container>
                }
            </Container>
            </div>
        );

    }
}

function mapStateToProps(state) {
    const article = state.ArticleReducer;
    const conf = state.ConfigReducer;
    return {
        articles: article.articles,
        checkpoint: article.next_checkpoint,
        loading: article.loading_articles,
        article_info: article.article_info,
        articlesAvailable: article.articles_available,
        offsetTop: conf.offsetTop,
    }
}

export default connect(mapStateToProps,{
    getArticle,
    getArticles,
    gotoArticle
})(ArticlePage);