import {LOADING_ARTICLES, ARTICLE_INFO, ARTICLES_RECEIVED, ARTICLE_FILTER, ARTICLES_AVAILABLE, MANAGE_ARTICLES_RECEIVED, NEXT_CHECKPOINT} from "../actions/types";

const INITIAL_STATE = {
    article_info: null,
    loading_articles: false,
    articles: [],
    manage_articles: [],
    filter:"",
    articles_available: true,
    batch_checkpoint : "",
    next_checkpoint:"",
};

export default (state = INITIAL_STATE, action) => {
    switch(action.type){
        case LOADING_ARTICLES:
            return {...state, loading_articles: action.loading_articles}
        case ARTICLE_INFO:
            return  {...state, article_info: action.payload, loading_articles: action.loading_articles};
        case ARTICLES_RECEIVED:
            return {...state, articles: [...state.articles, ...action.articles], loading_articles: action.loading_articles, articles_available: action.articles_available};
        case MANAGE_ARTICLES_RECEIVED:
            return {...state, manage_articles: action.articles, loading_articles: action.loading_articles};
        case ARTICLE_FILTER:
            return {...state, filter: action.payload};
        case ARTICLES_AVAILABLE:
            return {...state, articles_available: action.payload};
        case NEXT_CHECKPOINT:
            return {...state, next_checkpoint: action.checkpoint};
        default:
            return state;
    }
};
