import {LOADING_ARTICLES, ARTICLES_RECEIVED, ARTICLE_INFO, ARTICLE_FILTER, MANAGE_ARTICLES_RECEIVED, NEXT_CHECKPOINT} from "../actions/types";

const endpoint_remote = "https://aqueous-coast-74759.herokuapp.com/";
const endpoint_local = "http://localhost:8080/";
const endpoint = endpoint_local;

function requestingArticles(){
    return {
        type: LOADING_ARTICLES,
        loading_articles: true,
    }
}

function receiveArticles(_articles, lastbatch){
  return {
    type: ARTICLES_RECEIVED,
    articles: _articles,
    articles_available: !lastbatch,
    loading_articles: false,
  }
}

function receiveManageArticles(_articles){
    return {
        type: MANAGE_ARTICLES_RECEIVED,
        articles: _articles,
        loading_articles: false,
    }
}

function setNextCheckpoint(_articles){
    return {
        type: NEXT_CHECKPOINT,
        checkpoint: new Date(_articles[_articles.length-1].date).toISOString(),
    }
}

function setArticleInfo(article){
    return {
        type: ARTICLE_INFO,
        payload: article,
        loading_articles: false,
    }
}

export function gotoArticle(article){

    return (dispatch) => {
        dispatch(setArticleInfo(article));
    }
}

export function setArticleFilter(filter){

    return (dispatch) => {
        dispatch({
            type: ARTICLE_FILTER,
            payload: filter
        })
    }
}

