export function getArticles(ids,checkpoint){

        let config = {
        method: 'GET',
        headers: { 'Content-Type':'application/x-www-form-urlencoded', 'batch_checkpoint':checkpoint}
        };

        return (dispatch) => {
        dispatch(requestingArticles());
        return fetch(endpoint + 'getArticles' , config)
          .then(response =>
            response.json()
                .then(articles => ({ articles, response }))
                ).then(({ articles, response }) =>  {

    // TODO Fall into response not ok, all is ok atm
            if (response.status !== 200) {
              // If there was a problem, we want to
              // dispatch the error condition
                console.log("Error getting articles");

              //dispatch(loginError(user.message));
              return Promise.reject(articles)
            } else {
                let docs = [];
                // CHECK FOR DUPICATES
                articles.articles.forEach(article => {
                    if(!ids.includes(article._id)){
                        docs.push(article);
                    }
                });

                dispatch(receiveArticles(docs, articles.lastBatch));
                dispatch(setNextCheckpoint(docs));
                //articles = articles.sort((a,b) => new Date(b.date) - new Date(a.date));
              // If login was successful, set the token in local storage
              //localStorage.setItem('articles', articles); // TODO remember to look after these on app entry
              // Dispatch the success action
             // dispatch(receiveLogin(user));
            }
      }).catch(err => console.log(err))
    }

}

export function getArticlesByKeyWord(ids, keyword, callback = null){

    let config = {
        method: 'GET',
        headers: { 'Content-Type':'application/x-www-form-urlencoded'}
    };

        return (dispatch) => {
        dispatch(requestingArticles());
        return fetch(endpoint + 'getArticles/'+keyword , config)
          .then(response =>
            response.json()
                .then(articles => ({ articles, response }))
                ).then(({ articles, response }) =>  {

    // TODO Fall into response not ok, all is ok atm
            if (response.status !== 200) {
              // If there was a problem, we want to
              // dispatch the error condition
                console.log("Error getting articles");

              //dispatch(loginError(user.message));
              return Promise.reject(articles)
            } else {
                let docs = [];
                articles.forEach(article => {
                    if(!ids.includes(article._id)){
                        docs.push(article);
                    }
                });
                dispatch(receiveArticles(docs, false));

                if(callback != null){
                    callback();
                }
            }
      }).catch(err => console.log(err))
    }
}