import React,{Component} from "react";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import InputGroup from "react-bootstrap/InputGroup";
import FormControl from "react-bootstrap/FormControl";
import Button from "react-bootstrap/Button";
import ToggleButton from "react-bootstrap/ToggleButton";
import {setArticleFilter} from "../actions/article";
import Figure from "react-bootstrap/Figure";
import {FacebookIcon, FacebookShareButton, TwitterIcon, TwitterShareButton, LinkedinIcon, LinkedinShareButton} from "react-share";
import connect from "react-redux/es/connect/connect";
import {Route} from "react-router-dom";
import Card from "react-bootstrap/Card";
import {months} from "../utils";

class CustomArticle extends Component{
    constructor(){
        super();

        this.state={
            //paragraphs: 0,
            text: "",
            date:new Date(),
            layoutComponents: [],
            images: 0,
            paragraphs: 0,
        };

        this.renderEditor.bind(this);
        this.renderArticle.bind(this);
        this.getNewParagraph.bind(this);
        this.getNewImage.bind(this);
        this.getNewPoll.bind(this);
        this.updateComponentData.bind(this);
        this.moveComponent.bind(this);
    }

    /*componentDidUpdate(prevProps, prevState, snapShot){
        if(this.props.updateLayoutData){
            //console.log("text/img updated");
            console.log("UPD");
            this.setState({ updateFlag: false});
            let data = [];
            this.state.layoutComponents.forEach(component => {
                if(component.type==="img"){
                    data.push({"img_url":component.value});
                } else if(component.type === "paragraph"){
                    data.push({"paragraph":component.value});
                }
                // TODO
            });
            this.props.layoutDataCallback(data);
        }
    }*/

    componentDidMount(){

        if (this.props.layoutData !== null) {
            let loadData = [];
            this.props.layoutData.forEach((component, idx) => {
                loadData.push({type: component.type, key: idx, value: component.value});
            });
            this.setState({
                layoutComponents: loadData,
            });
            console.log(this.props.layoutData)
        }
        if(this.props.editArticle) {
            this.props.updateLayoutData(() => {
                let layoutData = [];
                this.state.layoutComponents.forEach(component => {
                    if (component.type === "img_url") {
                        layoutData.push({"type": "img_url", "value": component.value});
                    } else if (component.type === "paragraph") {
                        layoutData.push({"type": "paragraph", "value": component.value});
                    }
                    // TODO
                });
                return {layoutData};
            });
        }
    }

    renderArticle(){
        //let article = this.props.editArticle ? this.props.article : this.props.article_info;
        //console.log(article);
        return(
            <Container className="bg-white">
            <div>
                <div style={{margin: this.props.isMobileDevice ? '0' : '2rem', display:'flex', flexDirection:'column', alignItems:'center'}}>
                <div style={{padding: this.props.isMobileDevice ? '0' : '1rem', maxWidth: this.props.isMobileDevice ? 'auto' : '35rem', fontFamily:'Helvetica', color: 'white', position: 'absolute'}}>

                    <a style={{display: 'inline', fontSize: this.props.isMobileDevice ? '2rem' : '3rem', background: 'rgba(0,0,0,0.5)', fontWeight:'Bold'}}>{this.props.editArticle ? this.props.title : this.props.article_info.title}</a>
                {/*<p>{this.props.editArticle ? this.props.lead : this.props.article_info.lead}</p>*/}

                </div>
                    <Figure style={{flex:1, width: this.props.isMobileDevice ? window.innerWidth+'px' : '40rem'}}>
                        <Figure.Image
                            src={this.props.editArticle ? this.props.titleimg_url : this.props.article_info.titleimg_url} // WILL GENERATE ERRORS
                        />
                    </Figure>
                </div>

                <div style={{fontfamily:'Helvetica', marginLeft: (this.props.isMobileDevice ? window.innerWidth*0.0005+'rem' : '10rem'), marginRight: (this.props.isMobileDevice ? eval(window.innerWidth*0.0005)+'rem' : '10rem')}}>
                    <p style={{fontSize:'0.8rem'}}>

                        {months[new Date(this.props.editArticle ? this.props.date : this.props.article_info.date).getMonth()]}{" "}{new Date(this.props.editArticle ? this.props.date : this.props.article_info.date).getFullYear()}

                            {/*, By {this.props.editArticle ? this.props.author : this.props.article_info.author}*/}
                    </p>
                </div>
                {/*this.props.editArticle ? this.props.layoutData.text.split ('\n').map ((item, i) => <p key={i}>{item}</p>) :
                    this.props.article_info.text.split ('\n').map ((item, i) => <p key={i}>{item}</p>)
                */}
                {this.state.layoutComponents.map((component,idx) => (
                    <div key={idx}>
                        {component.type === "paragraph" &&
                            <div style={{fontfamily:'Helvetica', marginLeft: (this.props.isMobileDevice ? window.innerWidth*0.0005+'rem' : '10rem'), marginRight: (this.props.isMobileDevice ? eval(window.innerWidth*0.0005)+'rem' : '10rem')}}>
                                {component.value.split('\n').map ((item, i) => <p key={i}>{item}</p>)}
                            </div>
                        }
                        {component.type === "img_url" &&
                            <div style={{display:'flex', alignItems:'center', justifyContent:'center'}}>
                                <Figure style={{width: this.props.isMobileDevice ? window.innerWidth+'px' : '40rem'}}>
                                    <Figure.Image
                                        src={component.value} // WILL GENERATE ERRORS
                                    />
                                </Figure>
                            </div>
                        }
                    </div>
                ))}


            </div>


                <div className="bg-light p-4">

                    <Row className="row-cols-xs-1  mt-1">
                        {/*<FacebookShareButton className="m-2" url={window.location.href}><span><FacebookIcon size={32}/></span></FacebookShareButton>*/}
                        <TwitterShareButton className="m-2" url={window.location.href}><span><TwitterIcon size={32}/></span></TwitterShareButton>
                        <LinkedinShareButton className="m-2" url={window.location.href}><span><LinkedinIcon size={32}/></span></LinkedinShareButton>
                        <Route render={({history}) => (
                            <div className="m-2">
                                {(this.props.editArticle ? this.props.tags : this.props.article_info.tags).split(",").map((tag,index) => (
                                <a key={index} className="m-1 bg-secondary" href="#" onClick={(event) => {
                                        event.preventDefault();
                                        event.stopPropagation();
                                        if(!this.props.editArticle) {
                                            this.props.setArticleFilter(tag);
                                            history.push('/mainpage');
                                            window.scrollTo(0, this.props.offsetTop);
                                        }
                                    }}
                                    style={{color:'white', paddingLeft:"1rem", paddingRight:"1rem"}}
                                >
                                    {tag}
                                </a>
                                ))}
                            </div>
                        )}/>
                    </Row>

                    <div style={{paddingLeft:'1rem', paddingRight:'1rem', paddingBottom:'1rem', wordWrap:'break-word'}}>
                        Sources:
                        <br/>
                        <a>
                            {this.props.editArticle ? this.props.sources : this.props.article_info.sources}
                        </a>
                    </div>
                </div>
            </Container>
        );
    }

    updateComponentData(key,type,value){

        let components = this.state.layoutComponents;
        this.state.layoutComponents.forEach((component) => {
            if(component.key === key && component.type === type) {
                component.value = value;
                components[this.state.layoutComponents.indexOf(component)] = component;

                this.setState({
                    layoutComponents: components,
                });
            }
        });
    }

    moveComponent(arr, old_index, new_index) {
        arr.splice(new_index, 0, arr.splice(old_index, 1)[0]);
        return arr; // for testing
    };

    getNewParagraph(key, value){
        return(
            <Row key={key} className="mb-3">
                <Col md={9} xs={9}>
                <InputGroup>
                    <InputGroup.Prepend>
                        <InputGroup.Text>Paragraph</InputGroup.Text>
                    </InputGroup.Prepend>
                    <FormControl
                        as="textarea"
                        aria-label="Paragraph"
                        onChange={(event) => this.updateComponentData(key, "paragraph", event.target.value)}
                        defaultValue={value}/>
                </InputGroup>
                </Col>
                <Col md={1} xs={1}>

                    <Row>
                       <Button className="btn-secondary" size="sm" onClick={() => {
                            // MOVE UP
                           let idx = this.state.layoutComponents.findIndex(item => (item.key === key && item.type === "paragraph"));
                           if(idx>=0) {
                               let components = this.state.layoutComponents;
                               components = this.moveComponent(components, idx, idx - 1);
                               this.setState({
                                   layoutComponents: components,
                               });
                           }
                        }}
                        >&#x25B2;</Button>
                    </Row>
                    <Row>
                        <Button className="btn-secondary mt-1" size="sm" onClick={() => {
                            // MOVE DOWN
                           let idx = this.state.layoutComponents.findIndex(item => (item.key === key && item.type === "paragraph"));
                           if(idx<this.state.layoutComponents.length-1) {
                               let components = this.state.layoutComponents;
                               components = this.moveComponent(components, idx, idx + 1);
                               this.setState({
                                   layoutComponents: components,
                               });
                           }
                        }}
                        >&#x25BC;</Button>
                    </Row>
                </Col>
                <Col md={1} xs={1}>
                   <Button className="btn-danger" size="sm" onClick={() => {
                       let components = this.state.layoutComponents;
                       components = components.filter(function(value, index, arr){ return !(value.type === "paragraph" && value.key === key)});
                       this.setState({
                            layoutComponents: components,
                       })
                   }} >&#10060;</Button>

                </Col>

            </Row>
        );
    }

    getNewImage(key, value){
        return(
            <Row key={key} className="mb-3">
                <Col xs={9}>
                <InputGroup>
                    <InputGroup.Prepend>
                        <InputGroup.Text id="label">Image url:</InputGroup.Text>
                    </InputGroup.Prepend>
                    <FormControl
                        onChange={(event) => this.updateComponentData(key, "img_url", event.target.value)}
                        placeholder="image url"
                        aria-label="image url"
                        aria-describedby="label"
                        defaultValue={value}
                    />
                </InputGroup>
                </Col>
                <Col md={1} xs={1}>
                    <Row>
                       <Button className="btn-secondary" size="sm" onClick={() => {
                            // MOVE UP
                           let idx = this.state.layoutComponents.findIndex(item => (item.key === key && item.type === "img_url"));
                           if(idx>=0) {
                               let components = this.state.layoutComponents;
                               components = this.moveComponent(components, idx, idx - 1);
                               this.setState({
                                   layoutComponents: components,
                               });
                           }
                        }}
                        >&#x25B2;</Button>
                    </Row>
                    <Row>
                        <Button className="btn-secondary mt-1" size="sm" onClick={() => {
                            // MOVE DOWN
                           let idx = this.state.layoutComponents.findIndex(item => (item.key === key && item.type === "img_url"));
                           if(idx<this.state.layoutComponents.length-1) {
                               let components = this.state.layoutComponents;
                               components = this.moveComponent(components, idx, idx + 1);
                               this.setState({
                                   layoutComponents: components,
                               });
                           }
                        }}
                        >&#x25BC;</Button>
                    </Row>
                </Col>
                <Col md={1} xs={1}>
                   <Button className="btn-danger" size="sm" onClick={() => {
                       let components = this.state.layoutComponents;
                       components = components.filter(function(value, index, arr){ return !(value.type === "img_url" && value.key === key)});
                       this.setState({
                            layoutComponents: components,
                       })
                   }} >&#10060;</Button>
                </Col>
            </Row>
        );
    }

    getNewPoll(){
        return(
            <div></div>
        );
    }

    renderEditor(){
        return(
            <div style={{padding:0,margin:0,width:"100%"}}>

                <Button size="sm" className="btn-secondary mt-2 mb-2 mr-2" onClick={() => {
                    let components = this.state.layoutComponents;
                    let paragraphKey = this.state.paragraphs+1;
                    components.push({type:"paragraph", key:paragraphKey, value:""});
                    this.setState({
                        paragraphs: paragraphKey,
                        layoutComponents: components,
                    })
                }} >Add Paragraph</Button>

                <Button size="sm" className="btn-secondary m-2" onClick={() => {
                    let components = this.state.layoutComponents;
                    let imgkey = this.state.images+1;
                    components.push({type:"img_url",key:imgkey, value:""});
                    this.setState({
                        images: imgkey,
                        layoutComponents: components,
                    });
                }} >Add Image</Button>

                <Button disabled={true} size="sm" className="m-2 btn-secondary" onClick={() => console.log("Create Poll")} >Create Poll</Button>

                {/*hthis.renderParagraphs()*/}
                {this.state.layoutComponents.map((component,idx) => (
                    <div key={idx}>
                        {component.type === "img_url" ?
                        this.getNewImage(component.key,component.value)
                            : component.type === "paragraph" ?
                        this.getNewParagraph(component.key,component.value)
                            : component.type === "poll" ?
                        this.getNewPoll(component.key,component.value)
                            :
                        <a>undefined</a>
                        }
                    </div>
                ))}
            </div>
        );
    }

    render(){
        return(
        <div>
        {this.props.editArticle &&
            this.renderEditor()
        }
        {this.props.showArticle &&
            this.renderArticle()
        }
        </div>
        );
    }
}

function mapStateToProps(state) {
    const article = state.ArticleReducer;
    const conf = state.ConfigReducer;
    return {
        article_info: article.article_info,
        isMobileDevice: conf.isMobileDevice,
        offsetTop: conf.offsetTop,
    }
}

export default connect(mapStateToProps,{
    setArticleFilter,
})(CustomArticle);